.NaviBar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 24px;
  background-color: white;
}

.nav-area {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 1440px;
}

.nav-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nav-logo-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  cursor: pointer;
}

.nav-guest {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style-type: none;
  width: 100px;
  padding: 0;
  margin: 0;
}

.nav-login {
  color: black;
  cursor: pointer;
}

.nav-login svg {
  width: 40px;
  height: 40px;
}

.nav-middle {
  display: grid;
  align-items: center;
  justify-items: center;
  width: 100%;
  height: 200px;
}

.nav-logo {
  color: black;
  text-decoration: none;
}

.nav-logo h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Danfo", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-size: 70px;
  font-style: normal;
  font-variation-settings: "ELSH" 0;
  margin-top: 0;
  margin-bottom: 20px;
}

.nav-item {
  width: 100%;
}

.nav-list {
  display: flex;
  justify-content: space-evenly;
  list-style-type: none;
  width: 100%;
  padding: 0;
}

.nav-link {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.nav-link:hover {
  color: rgba(0, 0, 0, 0.5);
}

.nav-list li {
  /* margin-left: 24px;
  margin-right: 24px; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Do Hyeon", sans-serif;
  font-weight: 400;
  font-size: 32px;
  width: 112px;
  font-style: normal;
  cursor: pointer;
}

/* .nav-list li:last-child {
  margin-right: 10px;
} */

@media all and (min-width: 281px) and (max-width: 432px) {
  .nav-area {
    width: 100%;
  }

  .nav-middle {
    height: 160px;
  }

  .nav-logo h1 {
    font-size: 40px;
    margin: 0;
  }

  .nav-list li {
    width: 80px;
    font-size: 24px;
  }
}
